<template>
  <div class="login-form" @keyup.enter="login">
        <div class="form-group">
          <label class="form-label" for="input-username">Username</label>
          <input class="form-input" type="text" id="input-username" v-model="username" placeholder="Device Username">
        </div>
        <div class="form-group">
          <label class="form-label" for="input-password">Password</label>
          <input class="form-input" type="password" id="input-password" v-model="password" placeholder="Device Password">
        </div>

        <button class="btn btn-success loginButton" @click="login">Login</button>
        <div class="divider login-devider"></div>
        <div style="color: #bdc3c7; text-align: center;">Need help?<br>Read our <a href="http://onioniot.com/getstarted" target="_blank">getting started guide</a></div>
  </div>

</template>

<script>
export default {
  name: 'LoginForm',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    login: function () {
      this.$store.dispatch('login', {username: this.username, password: this.password}).then(resp => {
        this.$emit('success')
      })
    }
  }
}
</script>

<style scoped>
.login-form{
  width: 100%;
  max-width: 300px;
}

.login-form .form-group {
    margin-bottom: 10px;
}

.loginButton {
  width: 100%;
  margin-top: 20px;
}

.login-devider {
  margin-top: 20px;
  margin-bottom: 20px;
}
</style>



